// Fonts
$fonts-system: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
$fonts-serif: "Apple Garamond", "Baskerville", "Times New Roman", "Droid Serif", "Times","Source Serif Pro", serif;
$fonts-monospace: "SF Mono", "Monaco", "Inconsolata", "Fira Mono", "Droid Sans Mono", "Source Code Pro", monospace;

// Sizes
$max-width: 40em;
$border-radius: 0.2em;
$line-height: 1.4em; // set in ems
$rule-thickness: 1px;
$input-padding: 0.2em;
$break-point-l: 60em;

// Colours
$color-text-main: #222222;
$color-mid: #c0c0c0;
$color-links: #4169E1;
$color-code-background: #EEEEEE;

// Basic elements

body, html {
	overflow-x: hidden;
	font-family: $fonts-system;
	margin: auto;
	padding: $line-height * 0.5;
	line-height: $line-height;
	color: $color-text-main;
}
body {
	max-width: $max-width;
}
h1, h2, h3 {
	line-height: $line-height * 0.8;
	margin: ($line-height * 2) 0 ($line-height * 0.5) 0;
	& + & {
		margin-top: 0;
	}
}
code, pre {
	font-family: $fonts-monospace;
	background-color: $color-code-background;
	padding: 0.1em 0.3em;
	border-radius: $border-radius;
}
pre {
	overflow-x: scroll;
}
code {
	line-height: 0; // stop code spans from widening lines
}
hr {
    margin: ($line-height * 2) 0;
    border: none;
    border-top: $rule-thickness solid $color-mid;
}
a {
	text-decoration: none;
	color: $color-links;
}
input {
	font-family: $fonts-system;
	padding: $input-padding;
	border: $rule-thickness solid $color-mid;
	border-radius: $border-radius;
}
form {
	margin-bottom: $line-height;
}
button {
	font-family: $fonts-system;
}

// Common best practice classes
.visuallyhidden {
	border: 0;
	clip: rect(0 0 0 0);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px;
}
